
:root {
    --background-overlay-color: #ffffffe8;
    --menu-item-title: #DBCBCB;
    --link-background: #DBCBCB;
    --link-background-selected: #D4A66A;
    --link-hover: #FFB76B;
    --options-row-background: #DBCBCB;
    --options-border-color: #FFF6AA;
}

html {
}

.rootBackground {
    background-image: url("/paprika.jpg");
    background-position: center;
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: -1;
}

.autoMargin {
    margin: auto;
}

.SiteClickable {
    display: flex;
    padding: 4px 6px 4px 6px;
    background-color: var(--link-background);
}

.hidden {
    display: none;
}

.SiteClickable:hover {
    background-color: var(--link-hover);
    cursor: pointer;
    user-select: none;
}

.SiteClickable.selected {
    background-color: var(--link-background-selected);
}

.mainDiv {
    background-color: var(--background-overlay-color);
}

.CustomerMenuList {
    margin: auto;
    max-width: 40rem;
    padding-left: 1em;
    padding-right: 1em;
}

.MenuItemOptionsDiv {
    width: 100%;
    background-color: var(--link-background);
    border-radius: 0 0 2em 2em;
}

.MenuItemWidget {
    padding: 1em;
    text-align: center;
}

.MenuItemTitle {
    background-color: var(--menu-item-title);
    padding: 3px 1px 3px 5px;
    border-radius: 0em 25em 25em 0em;
}

.DescriptionRow {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

.MenuItemDescription {
    padding: 0.4rem;
}

.MenuItemExpandBtn {
    margin-top: 6px;
    padding: 1em;
    min-width: 2em;
    height: 2em;
    border-radius: 100em;
}

.MenuFoodModificationExandBtn {
    text-align: center;
}

.MenuFoodModificationMenu {
    text-align: left;
    margin-top: 6px;
    overflow: hidden;
    max-height: 0;
    transition: max-height 0.3s ease-out;
    display: inline-block;
    width: 60%;
}

.MenuItemOptions {
    background-color: var(--options-row-background);
    border-top: 3px var(--options-border-color);
    border-top-style: solid;
    margin: 5px 2px 0px 2px;
    min-width: 7em;    
    text-align: center;
    padding-bottom: 1em;
    display: inline-block;
    vertical-align: top;    
}

.MenuItemOption.selected:hover {
    background-color: var(--link-hover);
}

